.services .container {
    padding-top: 60px;
    padding-bottom: 70px;
}

.services {
    .service-item {
        flex: 0 0 33.33%;
        max-width: 33.33%;
        margin-bottom: 30px;

        .service-item-inner {
            background: var(--bg-black-100);
            border: 1px solid var(--bg-black-50);
            padding: 30px 15px;
            text-align: center;
            border-radius: 10px;
            transition: all 0.3s ease-in-out;

            &:hover {
                box-shadow: 0 0 20px rgba(48, 46, 77, .15);

                .icon {
                    background: var(--skin-color);

                    .service-icon {
                        color: var(--bg-black-100);
                        font-size: 30px;
                    }
                }

            }

            .icon {
                height: 60px;
                width: 60px;
                border-radius: 50%;
                display: flex;
                align-items: center;
                justify-content: center;
                margin: 0 auto 30px auto;
                text-align: center;
                transition: all .3s ease;

                .service-icon {
                    font-size: 40px;
                    color: var(--skin-color);
                    transition: all .3s ease;

                }
            }

            h4{
                font-size: 18px;
                margin-bottom: 15px;
                color: var(--text-black-900);
                font-weight: 700;
                text-transform: capitalize;
            }

            p{
                font-size: 16px;
                color: var(--text-black-700);
                line-height: 25px;
            }
        }
    }
}

@media(max-width:960px){
    .services .service-item{
        flex: 0 0 50%;
        max-width: 50%;
    }
  }

  @media(max-width:750px) {
    .services .service-item{
        flex: 0 0 100%;
        max-width: 100%;
    }
}