.contact .container{
    padding-top: 60px;
    padding-bottom: 70px;
}
.contact {
    .contact-title{
        color: var(--skin-color);
        text-align: center;
        font-size: 25px;
        margin-bottom: 20px;
    }
    .contact-sub-title{
        color: var(--text-black-900);
        text-align: center;
        font-size: 15px;
        margin-bottom: 60px;
    }
    .contact-info-item{
        flex: 0 0 25%;
        max-width: 25%;
        text-align: center;
        .icon{
            display: inline-block;
            .real-icon{
                font-size: 25px;
                color: var(--skin-color);
            }
        }
        h4{
            font-size: 18px;
            font-weight: 700;
            color: var(--text-black-900);
            text-transform: capitalize;
            margin: 15px 0 5px;
        }
        p{
            font-size: 16px;
            line-height: 25px;
            color: var(--text-black-700);
            font-weight: 400;
        }
    }
    .contact-form{
        flex: 0 0 100%;
        max-width: 100%;
        .col-6{
            flex: 0 0 50%;
            max-width: 50%;
        }
        .col-12{
            flex: 0 0 100%;
            max-width: 100%;
        }
        .form-item{
            margin-bottom: 30px;
            textarea.form-control{
                height: 140px;
                resize: none;
            }
            .form-control{
                width: 100%;height: 50px;
                border-radius: 25px;
                border: 1px solid var(--bg-black-50);
                background: var(--bg-black-100);
                font-size: 16px;
                color: var(--text-black-700);
                padding: 10px 25px;
                transition: all .3s ease;
                &:focus{
                    box-shadow: 0 0 20px rgba(48,46,77,.15);
                }
            }
        }

        .btn{
            height: 50px;
            padding: 0 50px;
            cursor: pointer;
        }

    }
}

@media(max-width:960px){
    .contact .contact-info-item{
        flex: 0 0 50%;
        max-width: 50%;
        margin-bottom: 30px;
        &:last-child{
            margin-bottom: 0;
        }
    }
  }

  @media(max-width:750px){
    .contact .contact-info-item{
        flex: 0 0 100%;
        max-width: 100%;
        margin-bottom: 30px;
        &:last-child{
            margin-bottom: 0;
        }
    }
    .contact .contact-form .col-6{
        flex: 0 0 100%;
        max-width: 100%;
    }
  }