
.home{
    min-height: 100vh;
    display: flex;
    color: var(--text-black-900);
    z-index: 1;
}




.home .container .row .home-info{
    flex: 0 0 60%;
    max-width: 60%;
    .hello{
        font-size: 28px;
        margin: 15px 0;
        span{
            font-family: 'Clicker Script' , cursive;
            font-size: 35px;
            font-weight: 700;
            color: var(--skin-color);
        }
    }
    .profession{
        font-size: 30px;
        margin: 15px 0;
        .typing{
            color: var(--skin-color);
        }
    }
    p{
        margin-bottom: 50px;
        font-size: 20px;
        color: var(--text-black-700);
    }
    .hire-me{
        margin-bottom: 50px;
    }
}




.home .container .row .home-img{
    flex: 0 0 40%;
    max-width: 60%;
    text-align: center;
    position: relative;
    &::before{
        content: '';
        position: absolute;
        height: 80px;
        width: 80px;
        border-top: 10px solid var(--skin-color);
        border-left: 10px solid var(--skin-color);
        top: -50px;
        left: 50px;
    }
    &::after{
        content: '';
        position: absolute;
        height: 80px;
        width: 80px;
        border-bottom: 10px solid var(--skin-color);
        border-right: 10px solid var(--skin-color);
        bottom: -50px;
        right: 50px;
    }
}
.home .home-img img{
    height: 400px;
    object-fit: cover;
    margin: auto;
    border-radius: 6px;
}




@media(max-width:1260px) {
    .home .home-img {
        display: none;
    }
    .home .container .row .home-info{
        flex: 0 0 100%;
        max-width: 100%;
    }
    .home .container{
        padding-bottom: 50px;
    }
}