.aside {
    width: 270px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    background: var(--bg-black-100);
    height: 100vh;
    padding: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-right: 1px solid var(--bg-black-50);
    transition: all .3s ease-in-out;
}

// .main-content {
//     padding-left: 270px;
// }

.section {
    min-height: 100vh;
    background: var(--bg-black-900);
    display: block;
    position: fixed;
    padding: 0 30px;
    z-index: 0;
    top: 0;
    left: 270px;
    right: 0;
    bottom: 0;
    overflow-y: auto;
    overflow-x: hidden;
    opacity: 1;
    transition: all .3s ease;
    &.active{
        z-index: 5;
        animation: slide 1s ease;
    }
    &.before{
        z-index: 3;
    }
}
@keyframes slide {
    0%{
        transform: translateX(100%);
    }
    100%{
        transform: translateX(0%);
    }
}

.shadow-dark {
    box-shadow: 0 0 20px rgba(48, 46, 77, 0.15);
}

.container {
    width: 100%;
    margin: auto;
}

.row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -15px;
    margin-right: -15px;
    position: relative;
}

.section-title {
    flex: 0 0 100%;
    max-width: 100%;
    margin-bottom: 60px;

    h2 {
        font-size: 40px;
        color: var(--text-black-900);
        font-weight: 700;
        position: relative;

        &::before {
            content: '';
            position: absolute;
            height: 5px;
            width: 60px;
            background: var(--skin-color);
            bottom: 0;
        }

        &::after {
            content: '';
            position: absolute;
            height: 5px;
            width: 30px;
            background: var(--skin-color);
            left: 0;
            bottom: -10px;
        }
    }
}

.padd-15 {
    padding-left: 15px;
    padding-right: 15px;
}

.btn {
    font-size: 25px;
    margin: 0px 25px;
    background: var(--skin-color);
    padding: 15px 35px;
    color: #fdf9ff;
    border-radius: 40px;
    border: none;
    display: inline-block;
    white-space: nowrap;
    transition: all 0.3s ease-in-out;

    &:hover {
        transform: scale(1.05);
    }
}

@media(max-width:1570px) {
    .aside {
        left: -270px;
        &.open{
            left:0;
        }
    }
    .section{
        left: 0;
    }

    .aside .nav-toggle{
        display: flex;
        left: 40px;
        top: 30px;
        &.open{
            left: 300px;
        }
    }
    // .main-content {
    //     padding: 0;
    // }
}



