.switcher {
    position: fixed;
    right: 0;
    top: 60px;
    z-index: 101;
    background: var(--bg-black-100);
    width: 200px;
    border: 1px solid var(--bg-black-50);
    box-shadow: 0 0 20px rgba(37, 39, 58, 0.15);
    padding: 10px;
    margin-right: 5px;
    border-radius: 6px;
    transition: all .3s ease-in-out;
    transform: translateX(calc(100% + 5px));

    &.open{
        transform: translateX(0%);
    }

    h4 {
        font-size: 16px;
        font-weight: 500;
        color: var(--text-black-900);
    }

    .s-icon {
        position: absolute;
        left: -50px;
        top: 0;
        background: var(--bg-black-100);
        border-radius: 50%;
        width: 30px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid var(--bg-black-50);
        cursor: pointer;
        color: var(--text-black-900);

    }

    .day-night {
        top: 40px;
        transition: all .3s ease-in-out;

    }

    .switcher-toggler {

        .setting-icon {
            animation: spin infinite 2s linear;
            color: var(--text-black-900);
        }
    }

    .colors {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        span {
            display: inline-block;
            height: 25px;
            width: 25px;
            border-radius: 50%;
            border: 1px solid var(--bg-black-50);
            cursor: pointer;
        }

        .color-1 {
            background: #ec1839;
        }

        .color-2 {
            background: #fa5b0f;
        }

        .color-3 {
            background: #37b182;
        }

        .color-4 {
            background: #1854b4;
        }

        .color-5 {
            background: #f021b2;
        }
    }

}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}