.portfolio .container{
    padding-top: 60px;
    padding-bottom: 70px;
}
.portfolio {
    .portfolio-header{
        flex: 0 0 100%;
        max-width: 100%;
        margin-bottom: 40px;
        h2{
            font-size: 18px;
            font-weight: 500;
            color: var(--text-black-700);
        }
    }

    .portfolio-item{
        flex: 0 0 33.33%;
        max-width: 33.33%;
        margin-bottom: 30px;
        .portfolio-item-inner{
            border: 6px solid var(--bg-black-100);
            border-radius: 10px;
            overflow: hidden;
            cursor: pointer;
            .portfolio-img{
                img{
                    width: 100%;
                    display: block;
                    object-fit: cover;
                }
            }
        }
    }
}

@media(max-width:960px){
    .portfolio .portfolio-item{
        flex: 0 0 50%;
        max-width: 50%;
    }
  }

  @media(max-width:750px) {
    .portfolio .portfolio-item{
        flex: 0 0 100%;
        max-width: 100%;
    }
}