.about .container {
    padding-top: 60px;
    padding-bottom: 70px;
}

.about {
    .about-content {
        flex: 0 0 100%;
        max-width: 100%;

        .about-text {
            flex: 0 0 100%;
            max-width: 100%;

            h3 {
                font-size: 24px;
                margin-bottom: 15px;
                font-weight: 700;
                color: var(--text-black-900);

                .name {
                    font-family: 'Clicker Script', cursive;
                    color: var(--skin-color);
                }

                .typing {
                    color: var(--skin-color);
                }
            }

            p {
                font-size: 16px;
                line-height: 1.5;
                color: var(--text-black-700);
                margin-bottom: 40px;
            }

        }
        

        .personal-info {
            flex: 0 0 60%;
            max-width: 60%;
            .buttons{
                flex: 0 0 100%;
                max-width: 100%;
            }

            .info-item {
                flex: 0 0 50%;
                max-width: 50%;
                margin-bottom: 30px;

                p {
                    font-weight: 600;
                    font-size: 16px;
                    padding: 10px 0;
                    color: var(--text-black-900);
                    border-bottom: 1px solid var(--bg-black-50);

                    span {
                        font-weight: 400;
                        color: var(--text-black-700);
                        margin-left: 4px;
                        display: inline-block;
                    }
                }
            }
        }

        .skills {
            flex: 0 0 40%;
            max-width: 40%;

            .skill-item {
                flex: 0 0 100%;
                max-width: 100%;
                margin-bottom: 10px;

                h5 {
                    line-height: 40px;
                    font-weight: 600;
                    font-size: 16px;
                    color: var(--text-black-900);
                }

                .progress {
                    width: 100%;
                    height: 8px;
                    border-radius: 4px;
                    background: var(--bg-black-50);
                    position: relative;

                    .progress-in {
                        position: absolute;
                        left: 0;
                        top: 0;
                        background: var(--skin-color);
                        height: 100%;
                        border-radius: 4px;
                    }

                    .skill-percent {
                        position: absolute;
                        color: var(--text-black-900);
                        right: 0;
                        top: -40px;
                        font-weight: 400;
                        line-height: 40px;
                    }
                }
            }
        }

        .education,
        .experience {
            flex: 0 0 50%;
            max-width: 50%;
            margin-top: 30px;

            h3.title {
                font-size: 24px;
                color: var(--text-black-900);
                margin-bottom: 30px;
            }

            .timeline-box {
                flex: 0 0 100%;
                max-width: 100%;

                .timeline {
                    background: var(--bg-black-100);
                    padding: 30px 15px;
                    border: 1px solid var(--bg-black-50);
                    border-radius: 10px;
                    width: 100%;
                    position: relative;

                    .timeline-item {
                        position: relative;
                        padding-left: 37px;
                        padding-bottom: 50px;

                        &:last-child {
                            padding-bottom: 0;
                        }

                        &::before {
                            content: '';
                            position: absolute;
                            height: 100%;
                            width: 1px;
                            left: 8px;
                            bottom: 0;
                            background: var(--skin-color);
                        }

                        .cercle-dot {
                            width: 18px;
                            height: 18px;
                            border-radius: 50%;
                            position: absolute;
                            left: 0px;
                            top: 0px;
                            background: var(--skin-color);
                        }

                        .timeline-date {
                            display: flex;
                            align-items: center;
                            margin-bottom: 12px;
                            font-weight: 400;
                            font-size: 14px;
                            color: var(--text-black-700);

                            .icon {
                                margin-right: 10px;
                            }
                        }

                        .timeline-title {
                            font-weight: 700;
                            font-size: 18px;
                            margin-bottom: 15px;
                            text-transform: capitalize;
                            color: var(--text-black-900);
                        }

                        .timeline-text {
                            line-height: 25px;
                            font-size: 16px;
                            color: var(--text-black-700);
                            text-align: justify;
                        }
                    }
                }
            }
        }
    }

}

@media(max-width:960px) {
    .about .about-content .personal-info .info-item p span {
        display: block;
        margin-left: 0;
    }
}

@media(max-width:800px) {
    .about .about-content .education,.about .about-content .experience,
    .about .about-content .personal-info ,.about .about-content .skills {
        flex: 0 0 100%;
        max-width: 100%;
    }
    .buttons .btn{
        margin: 20px 5px;
    }
}
@media(max-width:750px) {
    .about .about-content .personal-info .info-item p span {
        display: inline-block;
        margin-left: 5px;
    }
}

@media(max-width:550px) {
    .about .about-content .personal-info .info-item p span {
        display: block;
        margin-left: 0px;
        font-size: 14px;
    }
}
