.logo{
    position: absolute;
    top: 50px;
    font-size: 30px;
    text-transform: capitalize;
}
.logo a{
    color:var(--text-black-900);
    font-weight: 700;
    padding: 15px 20px;
    font-size: 30px;
    letter-spacing: 5px;
    position: relative;
}
.logo a::after{
    content: '';
    position: absolute;
    width: 30px;
    height: 30px;
    border-top: 6px solid var(--skin-color);
    border-right: 6px solid var(--skin-color);
    top: 0;
    right: 0;
}
.logo a::before{
    content: '';
    position: absolute;
    width: 30px;
    height: 30px;
    border-bottom: 6px solid var(--skin-color);
    border-left: 6px solid var(--skin-color);
    bottom: 0;
    left: 0;
}
.logo a span{
    font-family: 'Clicker Script', cursive;
    font-size: 40px;
}
.nav{
    margin-top: 50px;
}
.nav li{
    margin-bottom: 20px;
    display: block;
}
.nav li a {
    font-size: 16px;
    font-weight: 600;
    border-bottom: 1px solid var(--bg-black-50);
    color: var(--text-black-900);
    padding : 5px 15px;
    display: flex;
    
}
.nav li a.active{
    color: var(--skin-color);
}
.nav li a .icon{
    margin-right: 15px;
}



// mobile
.nav-toggle{
    height: 40px;
    width: 45px;
    border: 1px solid var(--bg-black-50);
    cursor: pointer;
    position: fixed;
    left: 300px;
    top: 20px;
    border-radius: 5px;
    background: var(--bg-black-100);
    display: none;
    align-items: center;
    justify-content: center;
    transition: all .3s ease;
    &.open{
        left: 300px;
    }
}
.nav-toggle span{
    height: 2px;
    width: 18px;
    background: var(--skin-color);
    display: inline-block;
    position: relative;
    transition: all .3s ease;
}
.nav-toggle.open span{
    background: var(--bg-black-100);
    display: inline-block;
    position: relative;
}
.nav-toggle span::after{
    content: '';
    position: absolute;
    height: 2px;
    width: 18px;
    background: var(--skin-color);
    display: inline-block;
    top: 6px;
    transition: all .3s ease;
}
.nav-toggle.open span::after{
    transform: rotate(-45deg);
    top: 0;
}
.nav-toggle.open span::before{
    transform: rotate(45deg);
    top: 0;
}
.nav-toggle span::before{
    content: '';
    position: absolute;
    height: 2px;
    width: 18px;
    background: var(--skin-color);
    display: inline-block;
    top: -6px;
    transition: all .3s ease;
}